
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.center {
  margin: 0 auto;
}
.options,
.form-control {
  width: 300px;
}
.title {
  color: #41b883;
}
.options {
  margin-top: 10px;
  margin-bottom: 10px;
}
.options fieldset {
    border: 1px solid #E6E6E6;
    border-radius: 5px;
}
.options fieldset label,
    .options fieldset input[type="text"],
    .options fieldset textarea {
      display: block;
      width: 100%;
      font-size: 16px;
      line-height: 21px;
}
.options fieldset label {
      margin-bottom: 10px;
      text-align: left;
}
.options fieldset label:last-child {
        margin-bottom: 0;
}
.options fieldset input[type="text"],
    .options fieldset textarea {
      padding: 5px 10px;
      border: 1px solid #E6E6E6;
      border-radius: 5px;
}
.options fieldset textarea {
      resize: vertical;
      min-height: 41px;
}
.options legend {
    color: #7B7B7B;
    text-align: left;
}



